import { useState, useEffect, useCallback, useMemo } from 'react'
import { inject, observer } from 'mobx-react'
import Icon from '../../components/Icon/Icon'
import './rebate.less'
import { SUPPORTED_WALLETS } from '../../utils/Constants'
import { formatAddress } from '../../utils/utils'
function Rebate({ wallet }) {
  const [token, setToken] = useState("USDC")
  const curConnector = useMemo(() => {
    const connector = localStorage.getItem("connector")
    if (connector) {
      return JSON.parse(connector)
    }
    return SUPPORTED_WALLETS.injected
  })
  return <div className='rebate-box'>
    <div className='rebate-box-info'>
      <div className='rebate-box-header'>
        <div className='rebate-avatar'>
          <Icon token="rebate" />
        </div>
        <div className='rebate-account-reward-info'>
          <div className='rebate-reward'>
            You have <span className='reward-btn'>0.00 <Icon token="deri-white" /></span> unclaimed {token} reward
          </div>
          <div className='rebate-code'>
            Referal Code:
          </div>
        </div>
        <div className='broker-recruiter'></div>
      </div>
      <div className='rebate-box-use-info'>
        <div className='rebate-box-user-info-header'>
          <div className='rebate-box-user-info-title'>
            My Referral Stats <span>
              （last updated: 03.04.2022 19:30:22 UTC）
            </span>
          </div>
          <div className='wallet-account-box'>
            <Icon token={curConnector.icon} width='20' />
            {formatAddress(wallet.detail.account)}
          </div>
        </div>
        <div className='rebate-box-use-info-box'>
          <div className='total-users-bg'>
            <div className='total-users-box'>
              <Icon token="rebate-total-users" />
              <div className='total-users-box-info'>
                <div className='total-users-box-info-num'>
                  56
                </div>
                <div className='total-users-box-info-title'>
                  Total Users
                </div>
              </div>
            </div>
          </div>
          <div className='total-fee-bg'>
            <div className='total-fee-box'>
              <Icon token="rebate-total-fee" />
              <div className='total-fee-box-info'>
                <div className='total-fee-box-info-num'>
                  56
                </div>
                <div className='total-fee-box-info-title'>
                  Total Transaction Fee
                </div>
              </div>
            </div>
          </div>
          <div className='total-rewards-bg'>
            <div className='total-rewards-box'>
              <Icon token="rebate-total-rewards" />
              <div className='total-rewards-box-info'>
                <div className='total-rewards-box-info-num'>
                  56
                </div>
                <div className='total-rewards-box-info-title'>
                  Total Rewards
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
      <div className='rebate-box-explained'></div>
    </div>
  </div>
}
export default inject('wallet')(observer(Rebate))